*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background: #ccc;

}


body::-webkit-scrollbar{

  width: .5px;
}

/* Card */

div#card{
  position: relative;
  width: 300px;
  height: 200px;
   border-radius: 20px;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.8);


}

div#card:hover div#card-infor{
  
  transition:all .5s ease ;
  padding: 2rem;
}

div#card div#card-infor{
position: absolute;
bottom: 0;
left: 0;
right: 0;
  background: rgba(0, 0, 0, 0.4);
  padding: 1rem;
  color: white;
  text-align: center;
  border-radius: 0 0 20px 20px ;
}

div#card-img{
  position: absolute;
  width: 300px;
  height:200px ;
  object-fit: cover;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  
}
div#card-img img{

  width: 300px;
  height: 200px;
  object-fit: cover;
  border-radius: 20px;
}


div#cards{
  display: grid;
  place-items: center;
  place-content: center;
  gap: .5rem;
  padding: .5rem;
  grid-template-columns: repeat(auto-fill, minmax(300px, auto));
}








header{

  width: 100%;
  padding: 1rem;
  color: white;
  display: flex;
  justify-content: space-between;
}

li{
  list-style: none;

}

a{
  text-decoration: none;

}
header ul {
  display: flex;
  column-gap: 1rem;
  
}  

@media (max-width: 310px) {

  header ul{
    display: grid;

    column-gap: 0;
  }
  header ul li{
    padding-bottom: 1rem;
  }
}

h3#main-title{

  color:#333;
  padding-left: 4rem;
  padding-top: 2rem;
  
}

header a{
  color: white;
}

div#cardbtn{

  display: flex;
  grid-gap: 1rem;
}

.active{

  font-weight: bold;
  border-bottom: 1px solid red;
}



/* flashcard */


 
div#flashCard{
position: relative;
 display: grid;
  width: 90%;
  height: 200px;
  background-color: #fff;
  margin: auto;
  padding: 1rem;
  color: #333;
  margin-top: 2rem;
  border-radius: 20px;
 place-items: center;
 box-shadow: 0 4px 2px -2px gray;
  
}

@media (max-width:500px) {

  div#flashCard{

    height: 400px;
  }
  
}

div#flashCard div#content{

  position: absolute;
  right: 2rem;
  top: 1rem;
  color: #333;
  
  font-size: 1.5rem;
  cursor: pointer;
}

img{

  filter: brightness(.90);
}
#spin{

  animation: spin 4s infinite linear;
}

@keyframes spin {
  from {
      transform: scale(1) rotate(0deg);
  }
  to {
      transform: scale(1) rotate(360deg);
  }
}

.rotate{
  transition: all 1s ease-in;
  transform: rotateX(360deg);
}

div#cardbtn{

  padding-top: 1rem;
  display: flex;
  grid-gap: 1rem;
  margin: auto;
  align-items: center;
  justify-content: center;
  justify-items: center;
}

div#cardbtn button{


  background: none;
  border: none;

  width: 100px;
  font-size: 2rem;
  cursor: pointer;
  color: black;
}


div#charadeCard{
  display: grid;
  place-items: center;
  place-content: center;
  text-align: center;
  padding: .5rem;
width: 300px;
height: 200px;
color: white;
  
}
div#charadeCard:hover{

  transition: all .5s ease-in-out;
  transform: scale(0.98);
  opacity: .9;
}

div#blocks {
  background-color: #3385D6;
  cursor: pointer;
  width: 100%;
  color: white;
  display: grid;
  place-items: center;
  place-content: center;
  height: 100vh;
}
div#blocks div#boxes{
  display: flex;
}
div#blocks div#title h1{
  font-weight: bold;
  font-size: 5rem;
  transition: all 1s ease-in-out;
  transform: rotateY(360deg);
}
div#icon{

 font-size: 3rem;
 color: #fff;
}

div#categories{

  display: grid;
  
  place-items: center;
 
  place-content: center;
  gap: .2rem;
  grid-template-columns: repeat(auto-fill, minmax(300px, auto));
}

h1#start{
  font-weight: bold;
  font-size: 5rem;
  text-orientation: upright;
}

h1#counter{
  font-weight: bold;
  font-size: 5rem;
  
}

div#game{

  background-color: #3385D6;
  display: flex;
  justify-content: space-between;
  color: white;
  height: 100vh;
  width: 100%;
}

div#game a{
  color:white;
  font-weight: bold;
}
div#rHand{
  
  display: grid;
 
  padding-top: 3rem;
  width: 300px;
  place-items: center;
  place-content: center;
  text-align: center;
}

div#rHand img{
  position: relative;
  width: 10rem;
  animation-name: imgLeft;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}



@keyframes imgLeft {
0%{left: -3rem }
50%{ left: -3rem}
75%{left: -2rem}
100%{left: 1rem}
  
}


div#lHand{
  
  display: grid;
 
  padding-top: 3rem;
  width: 300px;
  place-items: center;
  place-content: center;
  text-align: center;
}
div#lHand img{
  position: relative;
  width: 10rem;
  animation-name: imgLeft;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
} 





@media (max-width:500px) {
  
  div#rHand{
    width: 200px;
  }

  div#lHand{
    width: 200px;
  }
  div#lHand img{
    width: 50px;
  }
  div#rHand img{

    width: 50px;
  }
  
}






div#begin{
 position:relative;
  width: 100%;
  background: #3385D6;
  color: white;
  height: 100vh;
  
  display: grid;
  place-content: center;
  place-items: center;
  text-align: center;
}


div#sector{
  
  display: flex;
 
 
 
}



div#sector h1{
 
  padding: .5rem;
  
}

@media (max-width:300px) {
  div#sector h1{
    font-size: 3rem;
  }
}

div#sector div#one{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 50%;


}

div#sector div#two{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 50%;
  
}

div#stats{
  
  background-color: #3385D6;
  border-bottom: 10px solid rgb(66, 66, 227);
}

div#stats header{

  display: flex;
  place-items: center;
  place-content: center;
  overflow: hidden;
  justify-content: space-between;

}

div#final_score{

  display: flex;
  column-gap: .5rem;
  text-transform: uppercase;
}

div#final_score div#number{
  font-weight: bold;
  font-size: 2rem;

  border: 1px solid #fff;
  padding: 1rem;
  border-radius: 5px;
  background-color: rgb(66, 66, 227);
}


 section#points{

  display: flex;
    place-items: center;
    place-content: center;
   background-color: #ccc;
}

section#points div{
width: 100%;
display: grid;
place-items: center;
place-content: center;


font-weight: bold;

  
}

